.main_box {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  width: 1170px;
  height: 600px;
  gap: 30px;
  margin: 0 auto;
  margin-top: 60px;
  cursor: pointer;
}

.product_box {
  background-color: #000;
  color: #fafafa;
  position: relative;
}

.main_box .product_box img {
  position: absolute;
}

.main_box .product_box:nth-child(1) {
  grid-row: 1 / span 2;
  grid-column: 1 / span 2;
  height: 600px;
}

.main_box .product_box:nth-child(1) img {
  left: calc((570px - 517px) / 2);
  bottom: 0;
}

.main_box .product_box:nth-child(2) {
  grid-row: 1 / span 1;
  grid-column: 3 / span 2;
}

.main_box .product_box:nth-child(2) img {
  width: 432px;
  height: 286px;
  right: 0;
}

.main_box .product_box:nth-child(3) {
  grid-row: 2 / span 1;
  grid-column: 3 / span 1;
}

.main_box .product_box:nth-child(3) img {
  width: 190px;
  height: 221px;
  left: calc(50% - 190px / 2);
  top: calc(50% - 221px / 2 - 0.5px);
}

.main_box .product_box:nth-child(4) {
  grid-row: 2 / span 1;
  grid-column: 4 / span 1;
}

.main_box .product_box:nth-child(4) img {
  width: 201px;
  height: 203px;
  left: calc(50% - 201px / 2 - 0.5px);
  top: calc(50% - 203px / 2 - 1.5px);
}

.title {
  font-size: 24px;
  font-weight: 600;
}

.description {
  margin: 16px 0 !important;
}

.button {
  font-size: 14px;
  color: #ffffff;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid;
  padding: 0;
  border-radius: 0;
}
